* {
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
  margin: 0
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, Courier New, monospace
}

.App {
  text-align: center
}

.admin-header,
h1,
h2,
h3,
h4 {
  font-family: Elegant Typewriter, Another Typewriter, Times New Roman, Times, serif
}

.admin-header {
  align-items: center;
  background-color: #282c34;
  color: #fff;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  justify-content: center;
  padding: 10px
}

.admin-header * {
  font-family: Elegant Typewriter, Another Typewriter, Times New Roman, Times, serif
}

.admin-header nav {
  display: flex;
  gap: 8px;
  justify-content: center;
  width: 100%
}

.admin-header nav a {
  background-color: gray;
  border-radius: 5px;
  color: #fff;
  padding: 3px 5px;
  text-decoration: none;
  transition: opacity .5s
}

.admin-header nav a:hover {
  opacity: .6
}

.admin-header nav a.active,
.admin-header nav a.active:hover {
  background-color: #393939;
  opacity: 1
}


.admin-content {
  align-items: center;
  display: flex;
  flex-flow: column nowrap;
  margin: 0px;
  background-color: white;
  color: black;
  min-height: 80vh;
}

.admin-content * {
  color: black
}

.admin-content button {
  color: white;
}

content>* {
  margin: 10px
}

div.swatches-picker,
div.swatches-picker>div>div>div {
  height: inherit !important
}

.swatches-picker button{
  margin:0;
}

.frontend-container.head{
  width: 100vw;
  flex-grow: 1;
}

.frontend-container.left{
  flex-grow:0.5;
}

.frontend-container.right{
  flex-grow:0.5;
}



@media (max-width: 720px) {
  .admin-header nav {
    flex-flow: column nowrap;
  }
}

@font-face {
  font-family: "Another Typewriter";
  src: url("atwriter.ttf");
}

@font-face {
  font-family: "Elegant Typewriter";
  src: url("ELEGANT TYPEWRITER Regular.ttf");
  font-weight: 400;
}

@font-face {
  font-family: "Elegant Typewriter";
  src: url("ELEGANT TYPEWRITER Bold.ttf");
  font-weight: 700;
}

@font-face {
  font-family: "Elegant Typewriter";
  src: url("ELEGANT TYPEWRITER Light.ttf");
  font-weight: 300;
}