* {
    --bg-color: rgb(0, 0, 0);
    --accent-color: rgb(233, 206, 6);
    --alt-accent-color: #B99E1E;
    box-sizing: border-box;
    color: white;
    text-align: center;
}

body {
    /* background-color: var(--bg-color); */
    /* color:white; */
    background-color: var(--bg-color);

}

.right {
    border-left: 1px solid white;
}


button {
    display: flex;
    text-align: center;

    border-radius: 5px;
    padding: 15px;
    background-color: var(--alt-accent-color);
    border: none;
    cursor: pointer;

    margin-right: auto;
    margin-left: auto;
}

.quote {
    font-weight: bold;
    font-style: italic;
    font-size: 20px;
    font-family: 'Times New Roman', Times, serif;
    padding: 30px;
    border-radius: 15px;
    margin-top: 50px;
}

.startbutton {
    width: 100%;
    font-weight: bold;
    font-size: 1.3em;
    margin-top: auto;
    padding: 25px;
}

.logo {
    height: 400px;
}

.preview {
    display: flex;
    flex-flow: column nowrap;
    gap: 10px;
    font-size: 1.2em;
    margin: 0;
    background-color: var(--alt-accent-color);
    padding: 10px;
    padding-top: 30px;
}

.preview button {
    background-color: black;
    color: white;
}


.menu-navigation div {
    display: flex;
    height: 100%;
    align-items: center;
    margin-left: auto;
    margin-right: auto;

}

.menu-navigation a {
    color: white;
    text-decoration: none;
    padding-top: 18vh;
    padding-bottom: 18vh;
    margin: 2vh;
    width: 40vw;
    background-color: var(--accent-color);
    border-radius: 30px;
    font-size: 3em;
    font-weight: bolder;
}

.frontend-container {
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-around;
    align-items: stretch;
}

.frontend-container>div {
    flex-grow: 1;
    /* background-color: aqua; */
    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-start;
    align-items: stretch;
}

.left,
.right {
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: stretch;
}

.frontend-container .overview,
.frontend-container .preview {
    flex-grow: 1;
}

.settings {
    align-items: center;
    background-color: #000;
    display: flex;
    height: 50px;
    justify-content: center;
    opacity: .7;
    padding: 5px;
    transition: opacity .5s;
    width: 50px;
    position: fixed;
    top: 0;
    left: 0;
}

.size {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-evenly;
    gap: 10px;
}

.size button {
    text-align: center;
    flex-grow: 1;
}

.size .active {
    background-color: red;
}

.shuttingdown {
    display: flex;
    flex-flow: column nowrap;
}

.shuttingdown img {
    max-height: 50%;
}

.frontend-container .settings:hover {
    opacity: .5;
}

.frontend-container .drink-container {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    overflow-y: scroll;
}

.frontend-container .drink-container .drink {
    /* background-color: rgba(155, 155, 155, 0.5); */
    background-color: var(--accent-color);
    padding: 25px;
    transition: opacity .5s;
    border-radius: 15px;
    width: 28%;
}

.frontend-container .drink-container .drink.active {
    background-color: rgb(217, 255, 0);
}

.frontend-container .drink-container .drink button {
    /* background-color:rgb(0, 0, 0); */

}

.frontend-container .drink-container .drink:hover {
    opacity: .7;
}

.touch {
    height: 30vh;
}